@import "colors";

header {
  background: $sky;
  padding: 15px 60px;
  box-sizing: border-box; }

.header {
  display: flex;
  justify-content: flex-start;
  padding: 10px 0;
  a {
    color: #fff;
    display: block; }
  &__logo {
    flex: 2;
    // margin: 0 160px 0 0
    img {
      width: 197px;
      max-width: 100%; } }
  &__item {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5px 0 15px;
    font-size: 1em;
    color: #fff;
    &:nth-child(2) {
      flex: 2; } }
  &__contacts {
    font-size: 1.2em; }
  &__contacts, &__time, &__map, &__address {
    display: flex;
    align-items: center;
    img {
      width: 35px;
      margin-right: 15px; } } }

.map-link {
  position: relative;
  &:hover {
    text-decoration: underline; } }
